import Vue from 'vue'
import Vuex from 'vuex'
import {getRequest} from '../utils/api';
import {Notification} from 'element-ui';
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';

Vue.use(Vuex)

const now = new Date();

const store = new Vuex.Store({
    //相当于声明变量
    state: {
        routes: [],
        sessions: [],
        hrs: [],
        currentSession: null,
        filterKey: '',
        stomp: null,
        currentHr: JSON.parse(window.sessionStorage.getItem("user")),
        isDot: {}//Key:Value格式：Key 当前登录用户名 # 聊天对象名   Value true或false
    },
    //相当于修改属性
    mutations: {
        INIT_CURRENTHR(state, hr) {
            state.currentHr = hr;
        },
        initRoutes(state, data) {
            state.routes = data;
        },
        changeCurrentSession(state, currentSession) {
            Vue.set(state.isDot, state.currentHr.username + '#' + currentSession.username, false);
            state.currentSession = currentSession;
        },
        addMessage(state, msg) {
            let mss = state.sessions[state.currentHr.username + '#' + msg.to];
            if (!mss) {
                // state.sessions[state.currentHr.username + '#' + msg.to] = [];
                Vue.set(state.sessions, state.currentHr.username + '#' + msg.to, [])
            }
            ;
            state.sessions[state.currentHr.username + '#' + msg.to].push({
                content: msg.content,
                date: new Date(),
                self: !msg.notSelf
            });
        },
        INIT_DATA(state) {
            //这里是浏览器本地的历史聊天记录
            let data = localStorage.getItem('vue-chat-session');
            if (data) {
                state.sessions = JSON.parse(data);
            }
        },
        INIT_HR(state, data) {
            state.hrs = data;
        }
    },
    //action也可以修改属性，但是action：（1提交的是 mutation，而不是直接变更状态。（2可以包含任意异步操作
    actions: {
        connect(context) {
            context.state.stomp = Stomp.over(new SockJS('/ws/ep'));
            context.state.stomp.connect({}, success => {
                context.state.stomp.subscribe('/user/queue/chat', msg => {
                    let receiveMsg = JSON.parse(msg.body);
                    //如果没有选中Hr或者其他Hr发来消息，收到消息则弹出提示
                    if (context.state.currentSession == null || receiveMsg.from != context.state.currentSession.username) {
                        Notification.info({
                            title: '[ ' + receiveMsg.fromNickname + ' ]发来消息',
                            message: receiveMsg.content.length > 15 ? receiveMsg.content.substr(0, 15) + ' ...' : receiveMsg.content,
                            position: 'bottom-right'
                        });
                        Vue.set(context.state.isDot, context.state.currentHr.username + '#' + receiveMsg.from, true);
                    }
                    receiveMsg.notSelf = true;
                    receiveMsg.to = receiveMsg.from;
                    context.commit('addMessage', receiveMsg);
                })
            }, error => {

            })
        },
        initData(context) {
            context.commit('INIT_DATA');
            getRequest('/chat/hrs').then(resp => {
                if (resp) {
                    context.commit('INIT_HR', resp);
                }
            })
        }
    }
})

//监控sessions,当sessions发生变化时，将sessions存入sessionStorage
store.watch(function (state) {
    return state.sessions;
}, function (val) {
    localStorage.setItem('vue-chat-session', JSON.stringify(val));
}, {
    deep: true/*这个貌似是开启watch监测的判断,官方说明也比较模糊*/
})

export default store;