<template>
    <el-form
            v-loading="loading"
            element-loading-text="正在登录..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(255, 255, 255, 0.8)"

            :rules="rules"
            :model="loginForm" class="loginContainer" ref="loginForm">
        <h2 class="loginTitle">系统登录</h2>
        <el-form-item prop="username">
            <el-input type="text" v-model="loginForm.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item prop="password">
            <el-input type="password" v-model="loginForm.password" @keydown.enter.native="submitLogin"
                      auto-complete="off"></el-input>
        </el-form-item>
        <el-checkbox class="loginRemember" v-model="checked" label="记住密码"></el-checkbox>
        <el-button type="primary" style="width: 100%" @click="submitLogin">登录</el-button>
    </el-form>
</template>

<script>
    // import {postKeyValueRequest} from "../utils/api";//不用再导入，直接使用组件 this.方法名 调用
    export default {
        name: "Login",
        data() {
            return {
                loading: false,
                loginForm: {
                    username: 'admin',
                    password: '123'
                },
                checked: true,
                rules: {
                    username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}]
                }
            }
        },
        methods: {
            submitLogin() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {//then中的参数就是服务器返回的参数  注意：api.js中处理过这个服务器返回的数据
                            this.loading = false;
                            if (resp) {
                                //将用户信息存入sessionStorage
                                window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
                                // sessionStorage存储的数据只有在同一个会话中的页面才能访问并且当会话结束后数据也随之销毁。sessionStorage仅仅是会话级别的存储。
                                // 个人的理解是你在打开一个页面时记录sessionStorage,当你把页面或者浏览器关闭时session中的数据即销毁。

                                //登录成功时初始化currentHr
                                this.$store.commit('INIT_CURRENTHR', resp.obj);
                                //页面跳转
                                //$route相当于当前正在跳转的路由对象,可以从里面获取name,path,params,query等
                                //$router相当于一个全局的路由器对象,里面含有很多属性和子对象，例如history对象等
                                let path = this.$route.query.redirect;
                                this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
                            }
                        })
                    } else {
                        this.$message.error('请填写所有字段');
                        return false;
                    }
                });
            }
        }

    }
</script>
<style>
    * {
        margin: 0px;
        padding: 0px;
    }

    .loginContainer {
        width: 350px;
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        padding: 10px 40px 15px 40px;
        background: #ffffff;
        border: 1px solid #d6d6d6;
        box-shadow: 0 0 25px #eaeaea;
    }

    .loginTitle {
        font-size: 26px;
        text-align: center;
        color: #505458;
        margin: 15px auto 20px auto;
    }

    .loginRemember {
        text-align: left;
        margin-bottom: 15px;
    }
</style>